*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  background: #fff;
  color: #06142a;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

.c-notification {
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
}

.c-notification__body {
  background-color: #00bb8b;
  border-radius: 4px;
  color: #fff;
  display: inline-block;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.2);
  font-size: 18px;
  margin: 16px;
  padding: 24px;
}

.c-color-grid {
  display: grid;
  grid-template-columns: 1fr;
}

.c-color-block {
  padding: 48px;
  text-align: right;
}

.c-color-block__name {
  background: rgba(255, 255, 255, 0.4);
  border-radius: 4px;
  display: inline-block;
  margin: 0;
  padding: 8px;
}
